<template>
  <div class="col-md-12 p-0" id="projectdtl">
    <loader :is-visible="isLoading"></loader>
    <section id="divMainListData" class="main-content">
      <div id="PartialView_SiteMapLink" class="headingcont margintop10 marginleft_2" :data-title="title" style="display:none;">
      </div>
      <div class="user-guide-custom d-none">
        <div v-if="isUpdate" v-html="userGuideUpdate"></div>
        <div v-else v-html="userGuideAdd"></div>
      </div>
      <section class="main-content">
        <div class="container-fluid padding-t_10 px-0">
              <div class="col-md-12 p-0" id="myTabContent">
                <section class="page-action action-margin">
                  <div class="container-fluid p-0">
                    <div id="divShowlistingData" class="tablehead">
                      <div class="row">
                        <div class="col-md-12">
                          <div class="theme-primary partition-full">
                            <span class="p-name text-white">
                              <span v-if="isUpdate">{{ $t('SetupName') }}: {{ templateName }}</span>
                              <span v-else>{{ $t('SetupMapping') }}</span>
                            </span>
                            <span class="p-actions float-right">
                              <a href="javascript:;" :title="$t('Selectanyrecordtodelete')" id="DeleteMultipleChk" class="p-action-btn text-white iconenable" :class="{ disabled: selectedItems.length === 0 }" @click="deleteMultiple"><i class="fa fa-trash"></i><br>{{ $t('Delete') }}</a>
                              <a v-if="isUpdate" @click="backToListUrl" id="ancbacktolist" :title="$t('BacktoList')" class="p-action-btn text-white">
                                <i class="fa fa-angle-double-left"></i><br>{{ $t('BacktoList') }}
                              </a>
                            </span>
                          </div>
                        </div>
                      </div>
                      <div class="col-md-12 p-0 pt-2" :class="{ 'd-none': isUpdate }">
                        <div class="row">
                          <div class="col-lg-4">
                            <div class="form-group">
                              <label>{{ $t('Setup') }} {{ $t('Name') }}:<span class=""></span></label>
                              <input type="text" id="SetupName" name="TemplateName" class="form-control" v-model="templateName" readonly />
                            </div>
                          </div>
                        </div>
                      </div>
                      <div class="tab-pane call-detail fade p-0 active show pb-3" :class="{ 'mt-2': isUpdate }" id="ProjectList" role="tabpanel" aria-labelledby="ProjectList-tab">
                        <div class="listing">
                          <div class="table-responsive" id="projectdtl">
                            <table id="tblProjectList" class="table table-bordered dt-responsive nowrap mt-0 w-100" style="min-width:1200px;">
                              <thead class="thead-bg">
                                <tr>
                                  <th scope="col" width="30px" class="text-center">
                                    <div class="form-check form-check-inline">
                                      <div class="custom-control custom-checkbox custom-control-inline">
                                        <input type="checkbox" class="chkItems custom-control-input" id="chkAll" :disabled="isEditable" v-model="selectAll" @change="toggleSelectAll" />
                                        <label class="custom-control-label" for="chkAll"></label>
                                      </div>
                                    </div>
                                  </th>
                                  <th><span>{{ $t('ImportedField') }}</span></th>
                                  <th><span>{{ $t('DataType') }}</span></th>
                                  <th><span>{{ $t('fieldName') }}</span></th>
                                  <th><span>{{ $t('CustomColumnName') }}</span></th>
                                  <th class="text-center"><span>{{ $t('IsGroupable') }}</span></th>
                                </tr>
                              </thead>
                              <tbody>
                                <tr v-for="(item, index) in items" :key="item.setUpColumnId">
                                  <td class="text-center">
                                    <div class="form-check form-check-inline">
                                      <div class="custom-control custom-checkbox custom-control-inline">
                                        <input class="chkItems custom-control-input" type="checkbox" :id="'chk_' + item.setUpColumnId" :colName="item.importedColumn" :colPosition="item.columnPosition" :disabled="isEditable" :value="item.setUpColumnId" v-model="selectedItems" name="DeleteInputsMulti" />
                                        <label class="custom-control-label" :for="'chk_' + item.setUpColumnId"></label>
                                      </div>
                                    </div>
                                  </td>
                                  <td>
                                    <input type="text" name="ImportedColumn" id="ImportedColumn" class="form-control" :value="item.importedColumn" style="pointer-events:none;" readonly />
                                  </td>
                                  <td>
                                    <span>{{ item.dataType }}</span>
                                  </td>
                                  <td>
                                    <select :name="'Model_' + index + '__FieldName'" :id="'Model_' + index + '__FieldName'" class="form-control ddlOperator" :disabled="isEditable" v-model="item.fieldName">
                                      <option value="">{{ $t('Select') }}</option>
                                      <option v-for="field in getFieldsForDataType(item.dataType)" :key="field.value" :value="field.value" :data-first="field.firstAttribute">{{ field.text }}</option>
                                    </select>
                                  </td>
                                  <td>
                                    <input type="text" name="CustomName" id="CustomName" class="form-control" maxlength="100" v-model="item.customName" />
                                  </td>
                                  <td class="text-center">
                                    <div class="form-group">
                                      <label class="switch">
                                        <input type="checkbox" :data-check="item.isGroupable" :data-columnposition="item.templateId" name="IsGroupable" :value="item.isGroupable" class="dynamic custom-switch nochecked" :checked="item.isGroupable" @change="item.isGroupable = $event.target.checked">
                                        <span class="slider round">
                                          <span class="slider-yes text-left">{{ $t('Yes') }}</span>
                                          <span class="slider-no text-right pr-1">{{ $t('No') }}</span>
                                        </span>
                                      </label>
                                    </div>
                                  </td>
                                </tr>
                              </tbody>
                            </table>
                          </div>
                          <div class="text-right mt-3">
                          <button @click="submit" class="btn btn-success mr-2">
                              <em class="fa fa-floppy-o pr-2"></em>{{ $t('Save') }}
                          </button>
                          <button @click="backToListUrl" class="btn btn-danger">
                              <em class="fa fa-times pr-2" aria-hidden="true"></em>{{ $t('Cancel') }}
                          </button>
                        </div>
                        </div>
                      </div>
                      <div id="divadddata" class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0" style="display:none;"></div>
                    </div>
                  </div>
                </section>
              </div>            
          
        </div>
      </section>
    </section>
  </div>
</template>

<script>
import { locateBillingService } from '../../services/locateBillingService'

export default {
  name: 'SetupTemplate',
  data() {
    return {
      isLoading: false,
      title: '',
      userGuideUpdate: '',
      userGuideAdd: '',
      templateName: '',
      isEditable: 0,
      items: [],
      selectedItems: [],
      selectAll: false,
      fieldDropDownList: [],
      setupId: null,
      setupDetails: [],
      TotalRecords: 0,
      TotalPages: 0,
      CurrentPage: 1,
      PageSize: 10,
      PageNumber: 1
    };
  },
   created(){
    var vm=this;
    this.title = this.$t('BillingSetup');
    vm.isUpdate = this.$route.params.Update==true? true: false;
    this.isEditable = this.$route.params.isEditable==1 ? true:false;
    this.setupId = this.$route.params.setupId;
    this.FetchData();
  },
  methods: {
   FetchData: async function () {
    var vm = this;
    var params = `setupId=${this.$route.params.setupId}&isEditable=${this.$route.params.isEditable}`;

    // Check if vm.isUpdate is true
    if (vm.isUpdate) {
        // If isUpdate is true, call GetSetupUpdateRecords
        await locateBillingService.GetSetupUpdateRecords(params).then((response) => {
            // Update response fields
            this.fieldDropDownList = response.data.fieldDropDownList || [];
            this.isEditable = response.data.isEditable || 0;
            this.templateName = response.data.templateName || '';
            this.setupDetails = response.data.setupDetails || [];
            this.items = this.setupDetails;
            this.items.forEach(item => {
                item.fieldName = item.setUpColumnId || "";
            });
            if (this.items && this.items.length > 0) {
                this.TotalRecords = this.items[0].TOTAL_RECORDS || this.items[0].TOTALRECORDS || 0;
                this.TotalPages = Math.ceil(this.TotalRecords / this.PageSize);
                this.CurrentPage = this.PageNumber;
            } else {
                this.items = [];
                this.TotalRecords = 0;
            }
        });
    } else {
        // If isUpdate is false, call GetSetupTemplate
        await locateBillingService.GetSetupTemplate(params).then((response) => {
            // Update response fields
            this.fieldDropDownList = response.data.fieldDropDownList || [];
            this.templateName = response.data.templateName || '';
            this.setupDetails = response.data.setupDetails || [];
            this.items = this.setupDetails;
            this.items.forEach(item => {
                item.fieldName = item.setUpColumnId || "";
            });
            if (this.items && this.items.length > 0) {
                this.TotalRecords = this.items[0].TOTAL_RECORDS || this.items[0].TOTALRECORDS || 0;
                this.TotalPages = Math.ceil(this.TotalRecords / this.PageSize);
                this.CurrentPage = this.PageNumber;
            } else {
                this.items = [];
                this.TotalRecords = 0;
            }
        });
    }
},

    getFieldsForDataType(dataType) {
      return this.fieldDropDownList.filter(field => field.firstAttribute === dataType);
    },
    toggleSelectAll() {
      if (this.selectAll) {
        this.selectedItems = this.items.map(item => item.setUpColumnId);
      } else {
        this.selectedItems = [];
      }
    },
    backToListUrl(){
      var vm =this;
      vm.$router.push({ name: 'ImportFormatListing' });
    },
    submit() {
      var vm=this;
      this.isLoading = true;
      let val = '';
      let arrayAll = [];
      let arrayUnique = [];
      this.items.forEach((item, index) => {
        if (item.fieldName) {
          if (val.length > 0) val += '-';
          val += item.fieldName + '|' + this.getFieldText(item.fieldName) + '$' + item.dataType + '^' + item.customName + '~' + item.importedColumn + '%' + (item.isGroupable ? 1 : 0) + '%' + item.templateId + '%' + item.setUpColumnId;
          let text = this.getFieldText(item.fieldName);
          if (arrayAll.includes(text)) {
            arrayUnique.push(index);
          }
          arrayAll.push(text);
        } else {
          arrayUnique.push(index);
        }
      });
      if (val.length === 0) {
        this.ShowAlert('Please select at least one.', 'warning', true, this.$t('Alert'));
        this.isLoading = false;
        return;
      }
      if (!vm.isUpdate) {
        locateBillingService.SubmitTemplateMapping({ obj: val, setupId: this.setupId }).then(response => {
          if (response.data === true) {
             vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Setup')]), "Success", true, vm.$t('Alert'));
            this.backToListUrl();
          }
        }).catch(() => {
          this.isLoading = false;
        });
      } else {
        locateBillingService.UpdateTemplateMapping({ obj: val, setupId: this.setupId }).then(response => {
          if (response.data === true) {
              vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Setup')]), "Success", true, vm.$t('Alert'));
              this.backToListUrl();
          }
        }).catch(() => {
          this.isLoading = false;
        });
      }
    },
    getFieldText(value) {
      let field = this.fieldDropDownList.find(f => f.value === value);
      return field ? field.text : '';
    },
    deleteMultiple() {
      var vm=this;
      let val = '';
      debugger
      let name = '';
      this.selectedItems.forEach(id => {
        let item = this.items.find(i => i.setUpColumnId === id);
        if (item) {
          val += ',' + item.columnPosition;
          name += ',' + item.importedColumn;
        }
      });
      val = val.substring(1);
      name = name.substring(1);
      if (val.length > 0) {
        this.confirmR(this.$t('ConfirmDeleteRecord'), true, 'Alert', () => {
          if (!vm.isUpdate) {
            this.items = this.items.filter(item => !this.selectedItems.includes(item.setUpColumnId));
            this.ShowAlert(this.$t('RecordsDeleteSuccessfully'), 'Success', true, this.$t('Alert'));
            this.isLoading = false;
          } else {
            locateBillingService.DeleteColumn({ obj: val, setupId: this.setupId }).then(response => {
              if (response.data === true) {
                this.isLoading = false;
                this.items = this.items.filter(item => !this.selectedItems.includes(item.setUpColumnId));
                this.ShowAlert(this.$t('RecordsDeleteSuccessfully'), 'Success', true, this.$t('Alert'));
              }
              this.isLoading = false;
            }).catch(() => {
              this.isLoading = false;
            });
          }
        });
      } else {
        this.isLoading = false;
        this.ShowAlert(this.$t('Selectanyrecordtodelete'), 'warning', true, this.$t('Alert'));
      }
    },
    getRootURL() {
      return window.location.protocol + '//' + window.location.host;
    }
  }
};
</script>

<style scoped>
